<template>
    <div class="container">
        <h1>{{ $t("title.codecs") }}</h1>
        <p class="lead">
            {{ $t("description.codecs") }}
        </p>
        <div v-if="isError" class="alert alert-danger" role="alert">
            {{ $t("message.decodeError") }}
        </div>
        <div class="form-group">
            <textarea class="form-control" :placeholder="$t('text.text')" v-model="topText"  style="min-height: 250px"></textarea>
        </div>
        <div class="form-group row justify-content-md-center">
            <div class="form-group col-12 col-md-4 col-lg-2">
                <select id="inputState" class="form-control" v-model="codecsType">
                    <option v-for="item in codecsTypes">{{ item }}</option>
                </select>
            </div>
            <div class="form-group col-12 col-md-3 col-lg-2">
                <button type="button" class="btn btn-sm btn-primary btn-block" @click="encode">{{
                    $t("text.encode")}}
                </button>
            </div>
            <div class="form-group col-12 col-md-3 col-lg-2">
                <button type="button" class="btn btn-sm btn-primary btn-block" @click="decode">{{
                    $t("text.decode")}}
                </button>
            </div>
        </div>
        <div class="form-group">
            <TextAreaHasCopy :placeholder="$t('text.ciphertext')" minHeight="250px" :value="bottomText"></TextAreaHasCopy>
        </div>
    </div>
</template>

<script>
    import { Base64 } from 'js-base64';
    import Base32 from 'base-32';
    import punycode from 'punycode';
    import TextAreaHasCopy from '../../components/elements/TextAreaHasCopy.vue';

    export default {
        components: {
            TextAreaHasCopy
        },
        metaInfo() {
            return {
                title: this.$i18n.t('title.codecs'),
                meta: []
            }
        },
        data() {
            return {
                codecsTypes: ["base64", "base64url", "base32", "Punycode", "Punycode (Domain)"],
                codecsType: "base64",
                topText: "",
                bottomText: "",
                isError: false
            }
        },
        methods: {
            decode() {
                let targetText = "";
                this.isError = false;
                try {
                    switch (this.codecsType) {
                        case 'base64':
                            targetText = Base64.decode(this.topText);
                            break;
                        case 'base64url':
                            targetText = Base64.decode(this.topText);
                            break;
                        case 'base32':
                            targetText = Base32.decode(this.topText);
                            break;
                        case 'Punycode':
                            targetText = punycode.decode(this.topText);
                            break;
                        case 'Punycode (Domain)':
                            targetText = punycode.toUnicode(this.topText);
                            break;
                    }
                } catch {
                    this.isError = true;
                }

                this.bottomText = targetText;
            },
            encode() {
                let targetText = "";
                switch (this.codecsType) {
                    case 'base64':
                        targetText = Base64.encode(this.topText);
                        break;
                    case 'base64url':
                        targetText = Base64.encodeURI(this.topText);
                        break;
                    case 'base32':
                        targetText = Base32.encode(this.topText);
                        break;
                    case 'Punycode':
                        targetText = punycode.encode(this.topText);
                        break;
                    case 'Punycode (Domain)':
                        targetText = punycode.toASCII(this.topText);
                        break;
                }

                this.bottomText = targetText;
            }
        }
    }
</script>

