<template>
    <div>
        <div class="bd-clipboard">
            <h4>
                <a href="#" class="btn-clipboard badge badge-pill badge-primary bg-main-color text-area-has-copy"
                   data-toggle="tooltip" data-placement="top" :title="$t('text.clickToCopy')" @click="copy"><font-awesome-icon icon="copy"/></a>
            </h4>
        </div>
        <textarea class="form-control" :placeholder="placeholder" :style="{minHeight: minHeight}" readonly>{{ value }}</textarea>
    </div>
</template>

<script>
    import copy from 'copy-to-clipboard';

    export default {
        props: ['placeholder', 'minHeight', 'value'],

        data() {
            return {

            }
        },
        methods: {
            copy() {
                copy(this.value);
                $('.text-area-has-copy').attr('title', this.$i18n.t('text.copied'))
                    .tooltip('_fixTitle')
                    .tooltip('show');
            }
        },
        computed: {

        },
        watch: {

        },
        components: {

        },
        mounted() {
            let self = this;
            $('[data-toggle="tooltip"]').tooltip();
            $('.text-area-has-copy').on('hidden.bs.tooltip', function () {
                $('.text-area-has-copy').attr('title', self.$i18n.t('text.clickToCopy'))
                    .tooltip('_fixTitle')
            });
        }
    }
</script>

<style scoped lang="scss">
    .bd-clipboard {
        position: relative;
        display: none;
        float: right;
    }

    .btn-clipboard {
        position: absolute;
        top: -.75rem;
        right: -.75rem;
        z-index: 10;
        display: block;
        padding: .25rem .5rem;
    }

    @media (min-width: 768px) {
        .bd-clipboard {
            display: block;
        }
    }

</style>